import { Link, useNavigate } from "react-router-dom";
import Search from "./Search";
import { useDispatch, useSelector } from "react-redux";
import {Dropdown,Image} from 'react-bootstrap'
import { logout } from "../../actions/userAction";
// import Home from "./Home";

export default function Header(){
    const {isAuthenticated,user} = useSelector(state => state.authState);
    const {items:cartItems} = useSelector(state => state.cartState);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const logoutHandler = () =>{
        dispatch(logout);
    }
    return <nav className="navbar row">
                <div className="col-12 col-md-3">
                <div className="navbar-brand">
                    <Link to={'/'}><img width="150px" alt="FoodCart" src="/images/logo.png" /></Link>
                </div>
                </div>

                <div className="col-12 col-md-6 mt-2 mt-md-0">
                    <Search/>
                </div>

                <div className="col-12 col-md-3 mt-4 mt-md-0 text-center">

                    {isAuthenticated ? 
                    (<Dropdown className="d-inline"> 
                        <Dropdown.Toggle variant="default text-white pr-5" id="dropdown-basic">
                            <figure id="avatar avatar-nav">
                                <Image width="50px" src={user.avatar ?user.avatar : './images/default_avatar.jpg'}/>                                
                            </figure>
                            <span>{user.name}</span>                            
                        </Dropdown.Toggle>
                        <Dropdown.Menu>
                           {user.role === 'admin' && <Dropdown.Item onClick={() =>(navigate('/admin/dashboard'))} className="text-dark">Dashboard</Dropdown.Item>}
                            <Dropdown.Item onClick={() =>(navigate('/myprofile'))} className="text-dark">Profile</Dropdown.Item>
                            <Dropdown.Item onClick={() =>(navigate('/orders'))} className="text-dark">Orders</Dropdown.Item>
                            <Dropdown.Item onClick={logoutHandler} className="text-danger">LogOut</Dropdown.Item>
                        </Dropdown.Menu>
                    </Dropdown>) :
                <Link to='/login' className="btn" id="login_btn">
                    Login
                </Link>
                 }

                <Link to='/cart'><span id="cart" className="ml-3">Cart</span></Link>
                <span className="ml-1" id="cart_count">{cartItems.length}</span><br/>
                <Link to='/'><span id="home" style={{color:"green", fontFamily:"Arial Black"}} className="ml-3">Home</span></Link>
                <Link to='/producthome'><span id="producthome" style={{color:"green", fontFamily:"Arial Black"}}  className="ml-3">Products</span></Link>
                <Link to='/about'><span id="about" style={{color:"green", fontFamily:"Arial Black"}}  className="ml-3">About</span></Link>
                <Link to='/contact'><span id="contact" style={{color:"green", fontFamily:"Arial Black"}}  className="ml-3">Contact Us</span></Link>
        
                </div>
            </nav>
}